(function () {
    'use strict';
    var angular = require('angular');

    var cardDeclined = angular.module('app').component('cardDeclined', {
        template: require('./card-declined.component.html'),
        controller: ['appState', 'analyticService', function (appState, analyticService) {
            var vm = this;

            vm.$onInit = function () {
                analyticService.trackPageLoad(analyticService.pageList.dipDeclined);
                appState.clearAll();
            };
            vm.exit = function () {
                analyticService.buttonList.buttonClick.event_content = "Card Declined: Exit";
                analyticService.trackUserAction(analyticService.buttonList.buttonClick);
                window.location.href = "http://www.hsbc.co.in/";
            }
        }]
    });

    module.exports = cardDeclined.name;
})();



